<template>
  <div class="hello" align="center">
    <img src="../assets/kt_ci2.png" width="500px">
    <div class="form-group row container" align="center">
    <div class="col-lg-0"></div>
      <div class="col-lg-4" id="content-box">
        <div class="card">
          <div class="card-header">
            <b>📜 최근 게시글 </b>
          </div>
          <div class="card-body" style="cursor: pointer">
            <router-link tag="div" class="form-group" v-for="(post, index) in posts" :key="index" :to="'/postviewer?postId='+post.게시글번호">
              {{index+1}}. {{post.제목}}({{post.게시판명}}) <br> {{post.이름}}({{post.부서명}})
            </router-link>
          </div>
        </div>
      </div>
      <div class="col-lg-4">
        <div class="card"  id="content-box">
          <div class="card-header">
            <b>📃 HOT 게시글 </b>
          </div>
          <div class="card-body" style="cursor: pointer">
            <router-link tag="div" class="form-group" v-for="(hot, index) in hots" :key="index" :to="'/postviewer?postId='+hot.게시글번호">
              {{index+1}}. {{hot.제목}}({{hot.게시판명}}) - 추천 : {{hot.추천수}} <br> {{hot.이름}}({{hot.부서명}})
            </router-link>
          </div>
        </div>
      </div>
      <div class="col-lg-4"  id="content-box">
        <div class="card"  id="content-box">
          <div class="card-header">
            <b>📃 최근 코드리뷰(전체공개) </b>
          </div>
          <div class="card-body" style="cursor: pointer">
            <router-link tag="div" class="form-group" v-for="(code, index) in codes" :key="index" :to="'/codelist'">
              {{index+1}}. {{code.파일명}} <br> {{code.이름}}({{code.부서명}})
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  computed: {
        
    },
  created: function(){
    this.getCodes();
    this.getPosts();
    this.getHots();
  },
  methods: {
        getCodes: function() {
          this.$http.get(this.$config.targetURL + '/review/recent')
          .then(r=>{
          if(r.data.status == 'success'){
            console.log(r)
            this.codes = JSON.parse(r.data.result)
          }
        })
      },
      getPosts: function() {
          this.$http.get(this.$config.targetURL + '/board/post/recent')
          .then(r=>{
          if(r.data.status == 'success'){
            console.log(r)
            this.posts = JSON.parse(r.data.result)
          }
        })
      },
      getHots: function() {
          this.$http.get(this.$config.targetURL + '/board/post/hot')
          .then(r=>{
          if(r.data.status == 'success'){
            console.log(r)
            this.hots = JSON.parse(r.data.result)
          }
        })
      }
    },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      codes: [],
      posts: [],
      hots: []
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url(//fonts.googleapis.com/earlyaccess/nanumgothic.css);
@font-face {
  font-family: 'NanumGothic' ;
  src:url(//fonts.gstatic.com/ea/nanumgothic/v5/NanumGothic-Regular.eot);
  src:url(//fonts.gstatic.com/ea/nanumgothic/v5/NanumGothic-Regular.eot?#iefix) format('embedded-opentype'),
      url(//fonts.gstatic.com/ea/nanumgothic/v5/NanumGothic-Regular.woff2) format('woff2'),
      url(//fonts.gstatic.com/ea/nanumgothic/v5/NanumGothic-Regular.woff) format('woff'),
      url(//fonts.gstatic.com/ea/nanumgothic/v5/NanumGothic-Regular.ttf) format('truetype');
  font-weight : normal;
  font-style : normal;
}
@font-face {
    font-family: 'Olleh';
    src:url('../assets/Droid-Sans-Fallback.ttf') format('truetype');
    font-weight : normal;
    font-style : normal;
}
div {
 font-family: 'Olleh','NanumGothic';
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.hello {
    vertical-align: middle;
}
</style>



